/*=================================
	1. HEADER
=================================*/
.bg-fix {
  background-attachment: fixed;
  background-size: cover;
}
.parallax {
  background-size: cover;
}
.sticky-top {
  top: 100px;
}
.header {
  position: relative;
  z-index: 99999;
}
.header ul,
.header ol {
  margin-bottom: 0;
}
.header .container-fluid {
  padding-left: 30px;
  padding-right: 30px;
}
/*without top bar*/
.secondary-menu {
  float: right;
  padding: 17px 0;
  position: relative;
  z-index: 9;
  height: 80px;
  border-left: 1px solid #cccccc;
  margin-left: 10px;
}
.secondary-menu .btn-link {
  font-size: 22px;
  padding: 0;
  line-height: 22px;
  padding-top: 5px;
  outline: none !important;
  border: 0;
}
.secondary-menu .btn-link:hover {
  color: var(--primary);
  outline: none !important;
}
.secondary-menu .secondary-inner {
  display: inline-block;
}
.nav-search-bar {
  background-color: rgba(255, 255, 255, 0.95);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 999;
  display: none;
  overflow: hidden;
  padding: 0 15px;
  transition: all 0.5s;
}
.nav-search-bar.show {
  display: block;
}
.nav-search-bar.show form {
  transition: all 0.5s ease 0.5s;
  -moz-transition: all 0.5s ease 0.5s;
  -webkit-transition: all 0.5s ease 0.5s;
  opacity: 1;
}
.nav-search-bar form {
  width: 100%;
  max-width: 700px;
  margin: auto;
  position: relative;
  top: 50%;
  transition: all 0.5s;
  transition: all 0.5s;
  -webkit-transition: all 0.5s;
  -moz-transform: translate(0px, -50%);
  -moz-transform: translate(0px, -50%);
  -o-transform: translate(0px, -50%);
  -webkit-transform: translate(0px, -50%);
  opacity: 0;
}
.nav-search-bar .form-control {
  padding: 15px 60px 15px 15px;
  width: 100%;
  height: 70px;
  border: none;
  background: none;
  color: #000;
  font-size: 20px;
  border-bottom: 2px solid #000;
}
.nav-search-bar .form-control::-moz-placeholder {
  color: #000;
}
.nav-search-bar .form-control:-moz-placeholder {
  color: #000;
}
.nav-search-bar .form-control:-ms-input-placeholder {
  color: #000;
}
.nav-search-bar .form-control::-webkit-input-placeholder {
  color: #000;
}
.nav-search-bar form span {
  position: absolute;
  right: 15px;
  top: 50%;
  margin: -15px 0;
  height: 25px;
  font-size: 20px;
  cursor: pointer;
  color: #000;
}
.nav-search-bar > span {
  position: absolute;
  right: 15px;
  top: 15px;
  height: 25px;
  font-size: 20px;
  cursor: pointer;
  color: #000;
}
.header-lang-bx .dropdown-menu {
  top: 10px !important;
  right: 0;
  left: auto !important;
}
.header-lang-bx ul li {
  margin: 0 !important;
}
.header-lang-bx .flag:after {
  content: "";
  width: 20px;
  height: 12px;
  display: inline-block;
  background-size: cover;
  background-position: center;
  margin-top: 0px;
  margin-right: 2px;
}
.header-lang-bx .flag.flag-uk:after {
  background-image: url(../images/flag/united-kingdom.svg);
}
.header-lang-bx .flag.flag-us:after {
  background-image: url(../images/flag/united-states-of-america.svg);
}
/* Cart Button */
.cart-btn {
  width: 27px;
  height: 34px;
  border: 2px solid #020d26;
  display: inline-block;
  font-size: 12px;
  position: relative;
  padding: 11px 0 0 0;
  text-align: center;
}
.cart-btn:after {
  content: "";
  position: absolute;
  width: 12px;
  height: 6px;
  border: 2px solid #020d26;
  -moz-border: 2px solid #020d26;
  -webkit-border: 2px solid #020d26;
  -ms-border: 2px solid #020d26;
  -o-border: 2px solid #020d26;
  border-radius: 0px 0px 12px 12px;
  -moz-border-radius: 0px 0px 12px 12px;
  -webkit-border-radius: 0px 0px 12px 12px;
  -ms-border-radius: 0px 0px 12px 12px;
  -o-border-radius: 0px 0px 12px 12px;
  border-width: 0px 2px 2px 2px;
  top: 4px;
  left: 6px;
}
@media only screen and (max-width: 767px) {
}
@media only screen and (max-width: 480px) {
  .secondary-menu {
    margin: 0 1px 0 0;
    text-align: right;
  }
}

/*=================================
	2. HEADER TOPBAR
=================================*/
.top-bar {
  background-color: #020d26;
  border-bottom: 1px solid rgba(0, 0, 0, 0.08);
  color: #fff;
  padding: 11px 0;
}
.topbar-left {
  float: left;
}
.topbar-right {
  float: right;
}
.topbar-center,
.topbar-left,
.topbar-right {
  padding-left: 15px;
  padding-right: 15px;
}
.topbar-left ul li,
.topbar-right ul li {
  display: inline-block;
  position: relative;
}
.topbar-left ul li a,
.topbar-right ul li a {
  color: #c3cbdd;
  font-size: 15px;
  display: flex;
  align-items: center;
}
.topbar-left ul,
.topbar-right ul {
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: 14px;
}
.topbar-left ul li {
  padding-right: 10px;
  margin-right: 10px;
}
.topbar-right ul li {
  padding-left: 10px;
  margin-left: 10px;
}
.topbar-left ul li i {
  margin-right: 5px;
  font-size: 22px;
  color: var(--primary);
}
.topbar-left ul li:after,
.topbar-right ul li:after {
  position: absolute;
  width: 1px;
  height: 20px;
  background-color: #000;
  right: 0;
  content: "";
  top: 2px;
  opacity: 0.1;
}
.topbar-right ul li:after {
  right: auto;
  left: 0;
}
.topbar-right ul li:first-child:after,
.topbar-left ul li:last-child:after {
  content: none;
}
.header-lang-bx .btn:focus,
.header-lang-bx .btn:hover,
.header-lang-bx .btn {
  border: 0 !important;
}
.header-lang-bx .btn {
  padding: 0 10px 0px 0 !important;
  height: 18px;
  color: #000;
  font-size: 14px;
  font-weight: 400;
  text-transform: capitalize;
}
.header-lang-bx .btn:hover {
  color: #000;
}
.header-lang-bx.bootstrap-select.btn-group .dropdown-toggle .caret {
  right: 0;
  top: 5px;
}
.header-lang-bx .btn .fa-caret-down:before {
  content: "\f107";
}
@media only screen and (max-width: 991px) {
  .topbar-left {
    margin-bottom: 5px;
  }
  .topbar-right,
  .topbar-left {
    padding-left: 0;
    padding-right: 0;
    width: 100%;
    text-align: center;
  }
  .topbar-right ul li,
  .topbar-left ul li {
    margin: 0;
    padding: 0px 5px;
  }
}
@media only screen and (max-width: 767px) {
  .top-bar [class*="col-"] {
    width: 100%;
    text-align: right;
    padding: 10px 15px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  }
  .top-bar [class*="col-"]:last-child {
    border-bottom: none;
  }
  .top-bar {
    padding: 5px 0;
    display: none;
  }
  .topbar-center,
  .topbar-left,
  .topbar-right {
    display: black;
    width: 100%;
    text-align: center;
    padding: 3px 15px;
    padding-left: 15px;
    padding-right: 15px;
  }
  .topbar-left ul li {
    padding: 0 5px;
  }
}
/*=================================
	3. HEADER LOGO
=================================*/
.menu-logo {
  display: table;
  float: left;
  vertical-align: middle;
  padding: 0;
  color: #efbb20;
  margin-top: 0;
  margin-bottom: 0;
  margin-left: 0;
  margin-right: 0;
  width: 160px;
  height: 80px;
  position: relative;
  z-index: 9;
  transition: all 0.5s;
  -moz-transition: all 0.5s;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
}
.menu-logo > a {
  display: table-cell;
  vertical-align: middle;
}
.menu-logo img {
}
.menu-logo span {
  font-size: 20px;
  color: #efbb20;
  letter-spacing: 20px;
}
.sticky-header.active .menu-logo {
  transition: all 0.5s;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -o-transition: all 0.5s;
}
.sticky-header.active .logo-change .logo1,
.logo-change .logo2 {
  display: none;
}
.sticky-header.active .logo-change .logo2 {
  display: block;
}
@media only screen and (max-width: 767px) {
  .menu-logo,
  .sticky-header.active .menu-logo {
    width: 100px;
    max-width: 100px;
    height: 50px;
  }
  .menu-logo img {
    max-width: 100px;
  }
  .header .navbar-toggler,
  .header .sticky-header.active .navbar-toggler {
    margin: 14px 0 14px 15px;
  }
  .header .secondary-menu {
    padding: 7px 0;
    height: 50px;
  }
  .btn,
  .btn-secondry {
    font-size: 14px;
    padding: 9px 15px;
  }
  .secondary-menu .btn-link {
    padding-top: 7px;
  }
}
/*=================================
	4. MENU BAR
=================================*/
.menu-bar {
  background: #ffffff;
  width: 100%;
  position: relative;
}
.menu-bar .container {
  position: relative;
}
.navbar-toggler {
  border: 0 solid #efbb20;
  font-size: 16px;
  line-height: 24px;
  margin: 28px 0 30px 15px;
  padding: 0;
  float: right;
}
.navbar-toggler span {
  background: #000;
}
.menu-links {
  padding: 0;
}
.menu-links .nav {
  float: right;
}
.menu-links .nav i {
  font-size: 9px;
  margin-left: 3px;
  margin-top: -3px;
  vertical-align: middle;
}
.menu-links .nav > li {
  margin: 0px;
  font-weight: 500;
  position: relative;
}
.menu-links .nav > li > a {
  border-radius: 0px;
  color: #020d26;
  font-size: 17px;
  padding: 28px 10px 27px 10px;
  cursor: pointer;
  font-weight: 500;
  display: inline-block;
  position: relative;
}
.menu-links .nav > li > a:hover {
  background-color: transparent;
  color: #efbb20;
}
.menu-links .nav > li > a:active,
.menu-links .nav > li > a:focus {
  background-color: transparent;
}
.menu-links .nav > li.active > a,
.menu-links .nav > li.current-menu-item > a {
  background-color: transparent;
  color: #efbb20;
}
.menu-links .nav > li:hover > a {
  color: #efbb20;
}

.menu-links .nav > li:hover > a:after {
  content: "";
  width: 10px;
  height: 10px;
  background: #fff;
  top: 95%;
  position: absolute;
  left: 30px;
  transform: rotate(45deg);
  z-index: 11;
}
.menu-links .nav > li.home:hover > a:after {
  background: transparent;
}
.menu-links .nav > li.classic-menu:hover > a:after {
  background: transparent;
}
.menu-links .nav > li.no-sub:hover > a:after {
  content: none;
}
.menu-links .nav > li:hover > .sub-menu,
.menu-links .nav > li:hover > .mega-menu {
  opacity: 1;
  visibility: visible;
  margin-top: 0;
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  -ms-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.menu-links .nav > li > .sub-menu,
.menu-links .nav > li > .mega-menu {
  box-shadow: 0 0 40px rgba(0, 0, 0, 0.2);
}
.menu-links .nav > li .sub-menu {
  background-color: #ffffff;
  display: block;
  left: 0;
  list-style: none;
  opacity: 0;
  padding: 15px 0;
  position: absolute;
  visibility: hidden;
  width: 220px;
  z-index: 10;
  border-radius: 0;
  top: 100%;
}
.menu-links .nav > li .sub-menu li {
  position: relative;
}
.menu-links .nav > li .sub-menu li a {
  color: #585e6d;
  display: block;
  font-size: 15px;
  padding: 8px 25px;
  text-transform: capitalize;
  transition: all 0.15s linear;
  -webkit-transition: all 0.15s linear;
  -moz-transition: all 0.15s linear;
  -o-transition: all 0.15s linear;
  position: relative;
  font-weight: 500;
}
.menu-links .nav > li .mega-menu li a span,
.menu-links .nav > li .sub-menu li a span {
  position: relative;
}
.menu-links .nav > li .mega-menu li a span:after,
.menu-links .nav > li .sub-menu li a span:after {
  content: "";
  position: absolute;
  height: 1px;
  width: 0;
  background: var(--primary);
  bottom: 0;
  right: 0;
  transition: all 0.5s;
  -moz-transition: all 0.5s;
  -webkit-transition: all 0.5s;
  -ms-transition: all 0.5s;
  -o-transition: all 0.5s;
  -moz-transform-origin: right;
  -webkit-transform-origin: right;
  -ms-transform-origin: right;
  -o-transform-origin: right;
  transform-origin: right;
}
.menu-links .nav > li .mega-menu li:hover > a span:after,
.menu-links .nav > li .sub-menu li:hover > a span:after {
  width: 100%;
  -moz-transform-origin: left;
  -webkit-transform-origin: left;
  -ms-transform-origin: left;
  -o-transform-origin: left;
  transform-origin: left;
  left: 0;
  right: auto;
}
.menu-links .nav > li .sub-menu li:hover a {
  text-decoration: none;
}
.menu-links .nav > li .sub-menu li:hover > a {
  color: var(--primary);
}
.menu-links .nav > li .sub-menu li:last-child {
  border-bottom: 0px;
}
.menu-links .nav > li .sub-menu li > .sub-menu.left,
.menu-links .nav > li .sub-menu li:hover .sub-menu.left {
  left: auto;
  right: 220px;
}
.menu-links .nav > li > .sub-menu.left {
  left: auto;
  right: 0;
}
.menu-links .nav > li .sub-menu li .fa {
  color: inherit;
  display: block;
  float: right;
  font-size: 15px;
  position: absolute;
  right: 25px;
  top: 12px;
  opacity: 1;
}
.menu-links .nav > li .sub-menu li .fa.fa-nav {
  color: inherit;
  display: inline-block;
  float: none;
  font-size: 13px;
  margin-right: 5px;
  opacity: 1;
  position: unset;
  right: 10px;
  top: 12px;
}
.menu-links .nav > li .sub-menu li > .sub-menu {
  left: 220px;
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  -ms-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.menu-links .nav > li .sub-menu li:hover > .sub-menu {
  left: 220px;
  margin: 0px;
  opacity: 1;
  top: -1px;
  visibility: visible;
}
.menu-links .nav > li .sub-menu li:hover > .sub-menu:before {
  background-color: transparent;
  bottom: 0px;
  content: "";
  display: block;
  height: 100%;
  left: -6px;
  position: absolute;
  top: 0px;
  width: 6px;
}

/*=================================
	5. MEGA MENU
=================================*/
.menu-links .nav > li.has-mega-menu {
  position: inherit;
}
.menu-links .nav > li .mega-menu {
  background-color: #ffffff;
  display: table;
  left: 0px;
  list-style: none;
  opacity: 0;
  position: absolute;
  right: 0px;
  visibility: hidden;
  width: 100%;
  margin-top: 20px;
  z-index: 9;
  padding: 0;
}
.menu-links .nav > li .mega-menu > li {
  display: table-cell;
  padding: 30px 0 25px;
  position: relative;
  vertical-align: top;
  width: 25%;
}
.menu-links .nav > li .mega-menu > li:after {
  content: "";
  background-color: rgba(0, 0, 0, 0.1);
  position: absolute;
  right: 0px;
  top: 0px;
  display: block;
  width: 1px;
  height: 100%;
}
.menu-links .nav > li .mega-menu > li:last-child:after {
  display: none;
}
.menu-links .nav > li .mega-menu > li > a {
  color: #000;
