}
.timeline-view:after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 25%;
  margin-left: 0px;
  border-left: 2px dashed #ccc;
}
.timeline-area {
  position: relative;
  background-color: inherit;
  width: 100%;
  left: 0;
  display: flex;
}
.timeline-area:last-child .timeline-content {
  border: 0;
  margin-bottom: 0;
}
.timeline-content {
  max-width: 75%;
  flex: 0 0 75%;
  padding-left: 30px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
