}
.timeline-date .date-bx {
  font-weight: 600;
  color: #000;
  font-size: 40px;
  line-height: 30px;
  padding: 10px 0 20px 0;
  border-radius: 4px;
  position: relative;
  width: 100%;
}
.timeline-date .date-bx:before {
  content: "";
  position: absolute;
  width: 18px;
  height: 18px;
  right: -35px;
  background-color: white;
  border: 5px solid var(--primary);
  top: 15px;
  border-radius: 50%;
  z-index: 1;
}
