}
.timeline-date p {
  line-height: 24px;
  font-size: 16px;
  color: #888;
}

@media screen and (max-width: 600px) {
  .timeline-view:after {
    left: 31px;
  }
  .timeline-area {
    width: 100%;
    padding-left: 0;
    padding-right: 0;
    flex-direction: column;
  }
  .timeline-area:before {
    left: 60px;
    border: medium solid white;
    border-width: 10px 10px 10px 0;
    border-color: transparent white transparent transparent;
  }
  .timeline-view .left:after,
  .timeline-view .right:after {
    left: 15px;
  }
  .timeline-view .right {
    left: 0%;
  }
  .timeline-date .date-bx:before,
  .timeline-view:after {
    content: none;
  }
  .timeline-content {
    padding-bottom: 10px;
    margin-bottom: 30px;
  }
  .timeline-date,
  .timeline-content {
    padding-left: 0;
    max-width: 100%;
    flex: 0 0 100%;
