  text-align: center;
  line-height: 32px;
  font-weight: 600;
  border-radius: 30px;
  position: absolute;
  left: 40px;
  top: -2px;
}
.number-box .number-icon:after {
  content: "";
  position: absolute;
  top: 16px;
  left: -40px;
  width: 30px;
  height: 1px;
  background: var(--primary);
}
.number-box:after {
  content: attr(data-name);
  position: absolute;
  font-size: 90px;
  font-weight: 700;
  color: #000;
  opacity: 0.1;
  left: 0;
  top: -10px;
  line-height: 90px;
  z-index: -1;
}
.number-box .btn-link {
