/* Slider Banner */
.tt-slider {
  position: relative;
  overflow: hidden;
}
.tt-slider .slider-item {
  position: relative;
}
.tt-slider .slider-thumb img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.tt-slider .slider-thumb {
  height: 700px;
}
.tt-slider .slider-content {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  transform: translate(-50%, -50%);
}
.tt-slider .slick-arrow {
  background-color: rgba(0, 0, 0, 0.15);
  width: 100px;
  height: 100px;
  z-index: 9;
}
.tt-slider .slick-arrow:before {
  font-family: "fontawesome";
  line-height: 100px;
  font-size: 42px;
  color: #fff;
  display: block;
  background-color: transparent;
}
.tt-slider .slick-arrow.slick-prev:before {
  content: "\f104";
}
.tt-slider .slick-arrow.slick-prev {
  left: 0px;
}
.tt-slider .slick-arrow.slick-next:before {
  content: "\f105";
}
.tt-slider .slick-arrow.slick-next {
  right: 0px;
}
.tt-slider .slick-arrow:hover {
  background-color: #fff;
}
.tt-slider .slick-arrow:hover:before {
  color: #000;
}
.tt-slider .slider-content.text-center .content-inner {
  margin: 0 100px;
}

/* slider-one */
.slider-one .slider-content .sub-title {
  text-transform: uppercase;
  font-family: Barlow;
  line-height: 14px;
  font-size: 14px;
  letter-spacing: 3px;
  font-weight: 500;
  visibility: inherit;
  margin: 0px;
  padding: 8px 15px;
  display: inline-block;
  margin-bottom: 25px;
}
.slider-one .slider-content .title {
  font-size: 70px;
  line-height: 70px;
  font-family: "Barlow", sans-serif;
  font-weight: 300;
  margin-bottom: 40px;
}
.slider-one .slider-content .title strong {
  display: block;
  font-weight: 700;
}

/* slider-two */
.slider-two .slider-content .title {
  font-size: 70px;
  line-height: 80px;
  font-weight: 600;
  margin-bottom: 20px;
}
.slider-two .slider-content .sub-title {
  font-size: 16px;
  letter-spacing: 2px;
  font-weight: 400;
}
.slider-two .slider-content {
  margin-top: 50px;
}
.slider-two .slider-thumb {
  height: 900px;
}

/* slider-three */
.slider-three .slider-content .title {
  font-size: 60px;
  line-height: 70px;
  font-weight: 600;
  margin-bottom: 20px;
}
.slider-three .slider-content p {
  font-size: 22px;
  line-height: 32px;
}

/* slider-four */
.slider-four .slider-content .title {
  line-height: 70px;
  font-weight: 800;
  font-size: 70px;
  margin-bottom: 10px;
}
.slider-four .slider-content .sub-title {
  line-height: 20px;
  letter-spacing: 4px;
  font-weight: 500;
  font-size: 13px;
  margin-bottom: 30px;
}
.slider-four .slider-content .icon-cell {
  margin: 0 auto 30px;
}

@media only screen and (max-width: 991px) {
  .slider-one .slider-content .title {
    margin-bottom: 30px;
  }
  .tt-slider .slick-arrow {
    width: 60px;
    height: 60px;
  }
